<template>
  <div class="pdf-split">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>PDF Split</span>
          <el-icon><Document /></el-icon>
        </div>
      </template>
      
      <el-form :model="form" label-width="120px">
        <el-form-item label="Select PDF File" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="1"
            :on-change="handleFileChange"
            :before-upload="beforeUpload"
            accept=".pdf"
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              Drag PDF file here, or <em>click to upload</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                Only PDF files are allowed, max 100MB
              </div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="Split Pages">
          <el-input
            v-model="form.splitPages"
            placeholder="Format: 1-5,10-15 (leave empty to split by page)"
            clearable
          />
          <div class="form-tip">
            Supports range format like: 1-5,10-15, leave empty to split each page individually
          </div>
        </el-form-item>

        <el-form-item label="Output Format">
          <el-select v-model="form.outputFormat" placeholder="Select output format">
            <el-option label="PDF" value="pdf" />
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            :loading="loading"
            :disabled="!selectedFile"
            @click="handleSubmit"
          >
            {{ loading ? 'Processing...' : 'Start Split' }}
          </el-button>
          <el-button @click="resetForm">Reset</el-button>
        </el-form-item>
      </el-form>

      <!-- Results Display -->
      <div v-if="result" class="result-section">
        <el-divider>Processing Results</el-divider>
        <el-alert
          :title="result.msg"
          type="success"
          :closable="false"
          show-icon
        />
        <div class="result-info">
          <p><strong>Task ID:</strong> {{ result.data.taskId }}</p>
          <p><strong>Split Files Count:</strong> {{ result.data.splitFiles.length }}</p>
          <p><strong>Split Files:</strong></p>
          <ul>
            <li v-for="file in result.data.splitFiles" :key="file">
              {{ file }}
            </li>
          </ul>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            Download Results
          </el-button>
        </div>
      </div>
    </el-card>

    <section class="info-block">
      <h2>Feature Description</h2>
      <p>Split a PDF into multiple files by pages, ranges, or custom rules, supporting quick splitting and batch download.</p>
      <h3>Use Cases</h3>
      <ul>
        <li>Split a contract into chapter-based PDFs for sending and filing.</li>
        <li>Export only pages 10–20 of a PDF for sharing as an attachment.</li>
        <li>Split a scanned booklet into separate question files.</li>
      </ul>
    </section>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Document, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { downloadFile } from '@/utils/download'
import { validateFile, FILE_TYPES } from '@/utils/file'

const uploadRef = ref()
const loading = ref(false)
const selectedFile = ref(null)
const result = ref(null)

const form = reactive({
  splitPages: '',
  outputFormat: 'pdf'
})

// File selection handling
const handleFileChange = (file) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = file.raw
}

// Pre-upload validation
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // Prevent auto-upload
}

// Submit processing
const handleSubmit = async () => {
  if (!selectedFile.value) {
    ElMessage.warning('Please select a PDF file first')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)
    formData.append('splitPages', form.splitPages)
    formData.append('outputFormat', form.outputFormat)

    const response = await pdfApi.splitPdf(formData)
    result.value = response
    ElMessage.success('PDF split successful')
  } catch (error) {
    console.error('PDF split failed:', error)
  } finally {
    loading.value = false
  }
}

// Download results
const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const filename = 'split_files.zip'
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('Download started')
    } catch (error) {
      console.error('Download failed:', error)
      ElMessage.error('Download failed, please try again')
    }
  } else {
    ElMessage.warning('No file available for download')
  }
}

// Reset form
const resetForm = () => {
  form.splitPages = ''
  form.outputFormat = 'pdf'
  selectedFile.value = null
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-split {
  max-width: 800px;
  margin: 0 auto;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}

.result-section {
  margin-top: 20px;
}

.result-info {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.result-info p {
  margin: 8px 0;
}

.result-info ul {
  margin: 8px 0;
  padding-left: 20px;
}

.result-info li {
  margin: 4px 0;
}

.info-block { margin: 16px auto 0; padding: 16px; border: 1px solid var(--el-card-border-color, #ebeef5); background-color: var(--el-fill-color-blank, #fff); border-radius: var(--el-border-radius-base, 4px); box-sizing: border-box; max-width: 800px; }
</style>
